$main-grey: #CEE2E7;
$main-grey-alt: #c3ddde;
$main-grey-equ: #bdd5dc;
$darkgrey: #688A93;
$main-bggrey: #ECF3F5;
$grey-txt: #A4A4A4;
$main-yellow: #E6E183;
$main-green: #9DD6BE;
$main-red: #EEB4A7;
$main-blue: #8FBCFF;
$main-transparent: rgba(255, 255, 255, 0.95);
$title: #E7504F;

// Header
@import "./fontstyles";

* {
    margin: 0;
    padding: 30;
    box-sizing: border-box;
}

a {
    text-decoration: none;
    font-size: 16px;
    line-height: 24px;
    color: $darkgrey;

    &:hover {
        color: black;
    }
}

body {
    // font-family: 'PT Sans', sans-serif;
    font-family: 'PTSansExpert', sans-serif;
}

.hidden {
    display: none;
}

.wrap {
    display: flex;
    width: 100%;
    // padding: 50px 25px;
}

.container {
    // width: 80%;
    padding: 0 25px;
    // position: relative;
}

.flexcontainer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 1px;
}

.infocontainer {
    display: flex;
    width: 100%;
}

.innercontainer {
    display: flex;
    width: 100%;
}

.langmenu {
    padding: 40px 30px;
    white-space: nowrap;
    text-align: left;
    // width: 20%;
    background: $main-bggrey;
    list-style-type: none;
    // min-width: 235px;
    // border-radius: 10px;
    // column-count: 4;
    // column-gap: 50px;
    // column-rule: 1px solid white;
    // box-shadow: 3px 3px 12px rgba(0, 0, 0, 0.288);
}

.langlistbutton {
    text-decoration: none;
    padding: 3px 5px;
    font-size: 16px;
    text-align: left;
    color: $darkgrey;
    background: none;
    border: none;
    cursor: pointer;

    &:hover {
        color: black;
        background: $main-bggrey;
    }
}

.langparams {
    // width: 320px;
}

.langtitle {
    font-size: 64px;
    line-height: 83px;
    padding-top: 20px;
}

.langlist {
    font-size: 18px;
    line-height: 36px;
    width: 314px;
}

.langlisttitle {
    color: $title;
    // border-bottom: 1px solid $main-grey;
    padding-top: 30px;
}


.langinfo {
    font-size: 24px;
    line-height: 31px;
    padding: 40px 0 10px 50px;
    // width: 618px;
    // width: 70%;
}

.legendbg {
    position: sticky;
    top: 10px;
    z-index: 3000;
    padding: 17px;
    border-radius: 10px;
    background: $main-bggrey;
    display: flex;
    // flex-wrap: wrap;
    align-items: center;
    // margin: 40px 0 -57px 20px;
    margin: 20px 0 0 0;
    list-style-type: none;
    // column-count: 2;
    // column-gap: 20px;
}

.legenright {
    margin: 0 30px 0 0;
    display: flex;
    flex-wrap: nowrap;
}

.legendtextwrap {
    column-count: 2;
    column-gap: 10px;
    margin: 0;
    padding: 0 0 0 2vw;
    list-style-type: none;
}

.colorsample {
    width: 15px;
    height: 15px;
}

.legenditem {
    display: flex;
    padding: 3px;
}

.legendtext {
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    margin-left: 15px;
    white-space: nowrap;
}



// Togglebuttons

.togglebtns {
    align-self: flex-end;
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    background-color: white;
    border: none;
    padding: 6px 10px;
    margin: 9px 0;
    cursor: pointer;

    &:hover {
        background-color: $darkgrey;
        color: white;
    }
}

.togglebtnsactive {
    background-color: $darkgrey;
    color: white;
    cursor: default;
}



// Letters

.lettersblock {
    position: absolute;
    padding: 0 0 50px 0;
}

.smalltitle {
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    padding: 35px 15px 15px 0;
}

.altborder {
    outline: 2px solid $darkgrey;
    outline-offset: -1px;
    display: flex;
    flex-wrap: nowrap;
}

.altborder_in {
    outline: 3px solid white;
    outline-offset: -3px;
    display: flex;
    flex-wrap: nowrap;
    gap: 1px;
}

.smallunititle {
    position: absolute;
    width: 90px;
    padding: 8px;
}

.letter {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    text-align: center;
    width: 90px;
    height: 90px;
    background: $main-grey;

    &:hover {
        // position: relative;
        opacity: 0.7;
        filter: alpha(opacity=70);
    }
}

.bigletter {
    // font-family: 'Octava', 'PT Sans', sans-serif;
    font-family: 'PTSansExpert', sans-serif;
    font-size: 32px;
    line-height: 32px;
    padding-top: 30px;
}

.bigletterserifitalic {
    font-family: 'PTSerifExpertItalic', sans-serif;
}

.bigletteritalic {
    font-family: 'PTSansExpertItalic', sans-serif;
}

.bigletterserif {
    font-family: 'PTSerifExpert', sans-serif;
}

.notrussiansign {
    background: $main-grey;
}

.dialectsign {
    background: $main-green;
}

.oldersign {
    background: $main-red;
}

.lexicosign {
    background: $main-blue;
}

.altsign {
    background: $main-grey-alt;
}

.equsign {
    background: $main-grey-equ;
}

.featuresign {
    font-feature-settings: "aalt";
    -moz-font-feature-settings: "aalt";
    -webkit-font-feature-settings: "aalt";
}

.markplace {
    text-align: right;
    // padding: 0 5px 10px 10px;
    margin: 0 6px 10px 5px;
}

.altmark {
    font-size: 10px;
    padding: 1px 6px 3px 6px;
    border-radius: 10px;
    color: $grey-txt;
    background: $main-transparent;
}


